import React, { useState } from "react";
import {
  Grid,
  Box,
  Typography,
  Accordion,
  AccordionDetails,
  AccordionSummary,
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import bottomImage from "../assets/bottom-image.png";
import Footer from "./Footer";
import { textAlign } from "@mui/system";

const FaqComponent = () => {
  const [clickedQuestion, setClickedQuestion] = useState(null);
  const faqs = [
    {
      question: "What is AROMA (ERC20)?",
      answer:
        "Is an ERC20 token and is considered CryptoChefs bread & butter. Chefs can use AROMA for all types of transactions on CryptoChefs.",
    },
    {
      question: "What is a CryptoChefs NFT?",
      answer:
        "CryptoChef is the NFT token that is based on the ERC721 standard and issued on the Polygon & Ethereum blockchain. Each CryptoChef token is unique, represents the player, and serves as a basis to play the game. Every aspiring Chef needs to first acquire a CryptoChef NFT through our own store, or third-party marketplace such as OpenSea.",
    },
    {
      question: "What is a Recipe?",
      answer:
        "Recipes represent a Crypto Index Fund that a Chef believes will grow in value. It consists of a minimum of 3 and a maximum of 20 Cryptocurrencies (5% steps). Creating a recipe requires to own a CryptoChef NFT and to lock-up AROMA tokens.",
    },
    {
      question: "What is the Buffet?",
      answer:
        "As the name suggests, the buffet is where all the active recipes and their details are displayed. It also contains historical performance data of the recipe and the accumulated rewards.",
    },
    {
      question: "Why do I have to lock-up AROMA?",
      answer:
        "We want to promote Chefs that put the time and effort into creating amazing Recipes in which they really believe in. To prove your commitment, every Chef has to lock-up at least 200 AROMA tokens. The amount of locked-up tokens also influences your share of the reward pool - the more tokens you lock-up, the higher your share gets!",
    },
  ];

  const handleClick = (faq) => {
    if (clickedQuestion === faq.question) {
      setClickedQuestion(null);
    } else {
      setClickedQuestion(faq.question);
    }
  };

  return (
    <Grid container alignItems="flex-end">
      <Grid
        sx={{
          position: "absolute",
          zIndex: "999",
        }}
      >
        <Typography
          sx={{
            fontFamily: "LuckiestGuy-Regular",
            fontSize: "2.4vw",
            color: "secondary.main",
            textAlign: "center",
          }}
        >
          FREQUENTLY ASKED QUESTIONS
        </Typography>
        <Grid container pl="15vw" pt="5vh" pb="15vh" spacing={3}>
          {faqs.map((faq) => (
            <Grid item md={5}>
              <Accordion
                square={true}
                expanded={clickedQuestion === faq.question}
              >
                <AccordionSummary
                  expandIcon={
                    <ExpandMoreIcon
                      onClick={() => handleClick(faq)}
                      sx={{
                        color: "secondary.main",
                        opacity: "0.2",
                        fontSize: "2.5vw",
                      }}
                    />
                  }
                >
                  <Typography>{faq.question}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <Typography width="30.5vw" style={{ textAlign: "justify" }}>
                    {faq.answer}
                  </Typography>
                </AccordionDetails>
              </Accordion>
            </Grid>
          ))}
        </Grid>
        <Footer />
      </Grid>
      <img
        src={bottomImage}
        width="100%"
        style={{ position: "relative", bottom: "0" }}
      ></img>
    </Grid>
  );
};
export default FaqComponent;
