import React, { useState } from "react";
import { Box, Typography, Grid, Zoom } from "@mui/material";
import Carousel from "react-material-ui-carousel";
import "../fonts/LuckiestGuy-Regular.ttf";
import zombieChef from "../assets/CryptoChefs/chef1.png";
import hipsterChef from "../assets/CryptoChefs/chef2.png";
import ninjaChef from "../assets/CryptoChefs/chef3.png";
import back from "../assets/Arrows/back-arrow.png";
import forward from "../assets/Arrows/forward-arrow.png";

function MeetCryptoChefsComponent() {
  const [clicked, setClicked] = useState(false);
  const chefs = [
    {
      name: "Zombie Chef",
      image: zombieChef,
    },
    {
      name: "Hipster Chef",
      image: hipsterChef,
    },
    {
      name: "Ninja Chef",
      image: ninjaChef,
    },
  ];

  return (
    <Grid
      container
      flexDirection="column"
      alignItems="center"
      sx={{ backgroundColor: "secondary.main" }}
    >
      <Box
        sx={{
          width: "100%",
          height: "9vw",
          marginBottom: "2vw",
          backgroundColor: "primary.main",
          clipPath: "ellipse(67% 220% at 50% -120%)",
        }}
      ></Box>
      <Typography
        item
        sx={{
          fontFamily: "LuckiestGuy-Regular",
          fontSize: "2.4vw",
          mt: "5vw",
          mb: "4vw",
        }}
      >
        MEET THE CRYPTOCHEFS!
      </Typography>
      <Grid style={{ width: "55%", height: "26vw" }}>
        <Carousel
          interval="7000"
          navButtonsAlwaysVisible={true}
          indicatorIconButtonProps={{
            style: {
              color: "transparent",
            },
          }}
          navButtonsProps={{
            style: {
              backgroundColor: "transparent",
              marginTop: "-5vw",
              width: "7vw",
              height: "7vw",
            },
          }}
          PrevIcon={<img src={back} width="50%" alt="PrevIcon"></img>}
          NextIcon={<img src={forward} width="50%" alt="NextIcon"></img>}
        >
          {chefs.map((chef, index, array) => (
            <Box sx={{ display: "flex", justifyContent: "center" }}>
              {index === 0 ? (
                <img
                  src={array[array.length - 1].image}
                  alt={array[array.length - 1].name}
                  width="22%"
                  height="22%"
                  style={{
                    opacity: "0.3",
                  }}
                ></img>
              ) : (
                <img
                  src={array[index - 1].image}
                  alt={array[index - 1].name}
                  width="22%"
                  height="22%"
                  style={{
                    opacity: "0.3",
                  }}
                ></img>
              )}
              <Box
                width="14vw"
                height="29vw"
                sx={{
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "space-between",
                  alignItems: "center",
                }}
              >
                <img src={chef.image} width="100%" alt={chef.name}></img>
                <Typography fontSize="1.4vw">{chef.name}</Typography>
              </Box>
              {index === array.length - 1 ? (
                <img
                  src={array[0].image}
                  alt={array[0].name}
                  width="22%"
                  height="22%"
                  style={{
                    opacity: "0.3",
                  }}
                ></img>
              ) : (
                <img
                  src={array[index + 1].image}
                  alt={array[index + 1].name}
                  width="22%"
                  height="22%"
                  style={{
                    opacity: "0.3",
                  }}
                ></img>
              )}
            </Box>
          ))}
        </Carousel>
      </Grid>
      <Box
        sx={{
          width: "100%",
          height: "9vw",
          marginTop: "12vw",
          backgroundColor: "primary.main",
          clipPath: "ellipse(67% 220% at 50% 220%)",
        }}
      ></Box>
    </Grid>
  );
}

export default MeetCryptoChefsComponent;
